.random-planet {
    display: flex;
    padding: 1rem;
    border: 1px solid #444;
}

.random-planet .planet-image {
    width: 150px;
    height: 150px;
    border-radius: 10px;
    margin-right: 1rem;
}

.random-planet .list-group {
    margin: 1rem 0 0 1rem;
}

.random-planet .list-group-item {
    padding: 0.25rem;
}

.random-planet .list-group-item .term {
    margin-right: .5rem;
}